$tablet-width: 768px;


.App {
  text-align: center;
  background-color: #4b5254;
}

a {
  font-size: 18px;
}

.wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.main,
.footer {
  width: 320px;
  box-sizing: border-box;
  background-color: #f2f2f2;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 50px;
}

.main {
  &__title {
    padding-top: 20px;
    margin-bottom: 20px;
  }

  &__link {
    display: block;
    margin-bottom: 40px;
  }
}

.deposit,
.money,
.purchased {
  width: 280px;
}

.deposit {
  &__info {
    margin-top: 20px;
    margin-bottom: 20px;
    font-weight: 600;
  }

  &__value {
    text-align: center;
    display: block;
    background-color: #fff;
    width: 100%;
    min-height: 50px;
    border-bottom: 1px solid #000;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 30px;
    line-height: 47px;
    word-wrap: break-word;
    word-break: break-all;

    &--warning {
      color: #4bbf35;
      font-size: 20px;
    }
  }

}

.button {
  width: 100%;
  display: block;
  padding: 10px;
  margin-bottom: 20px;
  cursor: pointer;
}

.button:disabled {
  color: #a1a1a1;
  cursor: auto;
}

.banknote {
  display: flex;
  margin-bottom: 20px;
  align-items: center;

  &__input {
    width: 185px;
    box-sizing: border-box;
    min-height: 38px;
    padding: 10px;
  }

  &__rest {
    width: 185px;
    box-sizing: border-box;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    word-wrap: break-word;
    word-break: break-all;
    text-align: start;
    min-height: 30px;
    background-color: #fff;
  }

  &__error {
    width: 185px;
    color: #ff0000;
    font-size: 10px;
  }

  span {
    margin-left: 10px;
    margin-right: 10px;

  }
}

.denomination {
  width: 70px;
  box-sizing: border-box;
  padding: 10px;
  background-color: #c0e7e1;
}

.product {
  margin-bottom: 10px;
  background-color: #d7d7d7;
  padding-bottom: 10px;

  &__wrapper {
    display: flex;
  }

  &__title,
  &__price,
  &__amount {
    box-sizing: border-box;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    word-wrap: break-word;
    word-break: break-all;
    text-align: start;
    min-height: 30px;
    background-color: #fff;
  }

  &__title {
    width: 155px;
    background-color: #fff;
    border-bottom: 4px solid #66ff66;
    margin-right: 10px;
  }

  &__type {
    font-size: 10px;
    text-align: start;
  }

  &__price,
  &__amount {
    width: 70px;
    border-bottom: 4px solid #66ff66;
  }

  &__price {
    margin-right: 10px;
    font-weight: 600;

    span {
      font-weight: 400;
    }
  }

  &__amount {
    width: 50px;
  }
}

.product:hover:not([disabled]) {
  cursor: pointer;
}

.product[disabled] {

  .product__title,
  .product__price,
  .product__amount {
    color: #8a8c8d;
    cursor: auto;
    background-color: #c9c9c9;
    border-bottom: 4px solid #c9c9c9;

  }
}

.goods {
  list-style: none;
  overflow-y: auto;
  height: 300px;
}

.change {
  h4 {
    margin-bottom: 20px;
  }

  &__amount {
    display: block;
    width: 280px;
    min-height: 50px;
    background-color: #fff;
    word-wrap: break-word;
    word-break: break-all;
  }
}

.footer {
  border-top: 1px solid #c3c3c3;
  padding-top: 30px;

  &__title {
    color: #4b5254;
    margin-bottom: 20px;

    span {
      display: block;
      font-weight: 400;
    }
  }
}

@media (min-width: $tablet-width) {

  .main,
  .footer {
    width: $tablet-width;
  }

  .deposit {
    border-right: 1px solid #000;
    padding-right: 50px;

    &__info {
      margin-bottom: 10px;
      margin-top: 0;
    }
  }

  .money {
    padding-left: 50px;
  }

  .change,
  .purchased {
    width: 280px;
  }

  .purchased {
    border-right: 1px solid #000;
    padding-right: 50px;
    margin-top: 50px;
  }

  .change {
    width: 280px;
    padding-left: 50px;
    margin-top: 50px;
  }

  .goods {
    height: 400px;
  }
}